      <p class="guanzhu">
        <img src="../assets/images/head.png" alt="">
        <span>严选好品</span>
        <span>+ 特别关注</span>
      </p>
    </div>
    <div class="footer">
      <p>小牛连帽设计，可爱时尚，御寒保暖。简约圆领设计，穿着舒适大方。整齐的下摆，做工 精致，彰显品质。加绒加厚设计，冬天再也比怕冷。</p>
      <p class="footer_table">
        <span><img src="../assets/xqy/buy_icon_star_white.png" alt=""><i>收藏</i></span>
        <span>加入购物袋</span>
