<style lang="less" scoped>
  .cjwt_wrap {
    flex: 1;
    background: #F8F8FF;
  }

  .cjwt_wrap>img {
    width: 100%;
    height: auto;
  }

  .center {
    background: #fff;
    padding: 66px 0;
  }

  .center>h4 {
