    color: #000;
    text-align: center;
  }

  .introduce {
    font-size: 30px;
    color: #777777;
    text-align: center;
    padding-top: 84px;
    padding-bottom: 72px;
  }

  .guanzhu {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .guanzhu img {
