  }
  .footer_table span:nth-child(1) {
    border-top: 1px solid #f6f6f6;
    width: 188px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    img{
      width: 40px;
    }
    i{
      height: 20px;
      line-height: 20px;
      padding-top: 16px;
    }
  }
  .footer_table span:nth-child(2) {
    border-top: 1px solid #f6f6f6;
    text-align: center;
    flex: 1;
    font-size: 32px;
   border-left: solid 1px #eee;
  }
  .footer_table span:nth-child(3) {
